#lorem-ipsum
{
  max-width: 600px;
  
  color: rgba(0, 0, 0, 0.87);
  
  margin: auto;
  margin-bottom: 50px;
}

button{
  background:#1AAB8A;
  color:#fff;
  border:none;
  position:relative;
  height:60px;
  font-size:1.6em;
  padding:0 2em;
  cursor:pointer;
  transition:800ms ease all;
  outline:none;
  margin-bottom:25px;
}

@media screen and (max-width: 622px) {
  button {
    padding:auto;
    height:auto;
  }
}

button:hover{
  background:#fff;
  color:#1AAB8A;
}
button:before,button:after{
  content:'';
  position:absolute;
  top:0;
  right:0;
  height:2px;
  width:0;
  background: #1AAB8A;
  transition:400ms ease all;
}
button:after{
  right:inherit;
  top:inherit;
  left:0;
  bottom:0;
}
button:hover:before,button:hover:after{
  width:100%;
  transition:800ms ease all;
}

@media screen and (max-width: 622px) {
  .container{
    width:auto;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom:25px;
    margin-top:25px;
  }
    .progressbar li {
     width:auto;
     font-size: 10px;
     margin-right:12px;
    }
    .progressbar li:before{
      font-size:15px;
    }
  #lorem-ipsum
  {
  margin-left: 20px;
  margin-right:20px;
  }
}

