.boxSECTION_1 {
    margin-top:25px;
    margin-bottom: 25px;
    box-sizing: border-box;
    color: rgb(73, 80, 87);
    text-decoration: none solid rgb(73, 80, 87);
    text-size-adjust: 100%;
    width: 100%;
    column-rule-color: rgb(73, 80, 87);
    perspective-origin: 118.656px 315.5px;
    transform-origin: 118.664px 315.5px;
    caret-color: rgb(73, 80, 87);
    border: 0px none rgb(73, 80, 87);
  outline: rgb(73, 80, 87) none 0px;
    padding:15px;
  }/*#boxSECTION_1*/
  
  .inner{
    width: 50%;
    margin: 0 auto;
  }

  .outer{
    width:100%;
  }

  @media screen and (max-width: 922px) {
    .inner{
      width:100%;
    }
  }
  
  .boxARTICLE_2 {
    bottom: 0px;
    box-sizing: border-box;
    color: rgb(73, 80, 87);
    text-decoration: none solid rgb(73, 80, 87);
    text-size-adjust: 100%;
    width: 100%;
    padding:35px;
    column-rule-color: rgb(73, 80, 87);
    perspective-origin: 118.656px 315.5px;
    transform-origin: 118.664px 315.5px;
    caret-color: rgb(73, 80, 87);
    border: 0px none rgb(73, 80, 87);
  outline: rgb(73, 80, 87) none 0px;
  }/*#boxARTICLE_2*/
  

  @media screen and (max-width: 622px) {
    .boxARTICLE_2 {
      padding:0px;
    }/*#boxARTICLE_2*/
  }
  
  
  .boxSPAN_3 {
    box-sizing: border-box;
    color: rgb(73, 80, 87);
    display: block;
    height: 8px;
    text-decoration: none solid rgb(73, 80, 87);
    text-size-adjust: 100%;
    width: 100%;
    column-rule-color: rgb(73, 80, 87);
    perspective-origin: 118.656px 4px;
    transform-origin: 118.664px 4px;
    caret-color: rgb(73, 80, 87);
    background: rgb(99, 230, 190) none repeat scroll 0% 0% / auto padding-box border-box;
    border: 0px none rgb(73, 80, 87);
    border-radius: 4px 4px 0 0;
  outline: rgb(73, 80, 87) none 0px;
    padding: 4px 0px;
  }/*#boxSPAN_3*/
  
  
  .boxDIV_4 {
    box-sizing: border-box;
    color: rgb(73, 80, 87);
    padding-top:25px;
    text-decoration: none solid rgb(73, 80, 87);
    text-size-adjust: 100%;
    width: 100%;
    column-rule-color: rgb(73, 80, 87);
    perspective-origin: 118.656px 267.5px;
    transform-origin: 118.664px 267.5px;
    caret-color: rgb(73, 80, 87);
    background: rgb(255, 255, 255) none repeat scroll 0% 0% / auto padding-box border-box;
    border-top: 0px none rgb(222, 226, 230);
    border-right: 1px solid rgb(222, 226, 230);
    border-bottom: 1px solid rgb(222, 226, 230);
    border-left: 1px solid rgb(222, 226, 230);
    border-radius: 0 0 4px 4px;
   outline: rgb(73, 80, 87) none 0px;
   padding:25px;
  }/*#boxDIV_4*/
  

  .box1{
      width:100%;
      margin-top:25px;
      margin-bottom: 25px;
      background: white;
  }
  .box2{
      padding-top:25px;
      padding-bottom: 10px;
  }

  .green{
    background: #7bdcb5;
    margin-top:0px;
  }